.RadioGroup {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  width: 100%;
  padding: 0 16px;
}

.RadioGroup p {
  margin-top: 0;
  -ms-flex-item-align: start;
      align-self: flex-start;
}

/* actual radio group */
.RadioGroup > div {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  width: 100%;
  padding: 6px;
  font-size: 1.2em;
}

/* input container */
.RadioGroup > div > div {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}

.RadioGroup > div > div > label {
  padding-right: 4px;
}


